import store from '@/store';

import Home from '@/views/Home';
import Project from '@/views/Project';
import Projects from '@/views/Projects';
import SplitHome from '@/views/SplitHome';
import HorizontalSplitHome from '@/views/HorizontalSplitHome';
import Studio from '@/views/Studio';
import Member from '@/views/Member';
import LinearHome from '@/views/LinearHome';

const routes = [
  {
    path: '/',
    name: 'Home',
    component: LinearHome,
  },

  {
    path: '/rga_project/:slug',
    redirect: '/projects/:slug'
  },

  {
    path: '/projects',
    component: Projects
  },

  {
    path: '/projects/:slug',
    component: Project
  },

  {
    path: '/studio',
    component: Studio,
  },

  {
    path: '/studio/:slug',
    component: Member,
  }
];

export default routes;



