<template>
  <article
    v-if="!isEmpty(company)"
    :class="$style.container"
  >

    <navigation-bar />

    <header
      :class="$style.header"
    >
      <cf-image
        :class="$style.logo"
        :asset="fields.logo"
      />
      <h1>{{ fields.name }}</h1>
    </header>

    <main
      :class="$style.content"
    >
      <section
        :class="$style.introduction"
        v-html="markdown(fields.introduction)"
      />

      <section
        :class="$style.showcase"
      >
        <h2>Featured Projects</h2>
        <project-list
          :class="$style.projects"
          :projects="fields.showcase"
        >
          <template
            slot-scope="{ project }"
          >
            <project-card
              :to="`/projects/${project.fields.slug}`"
            >
              <cf-image
                :asset="project.fields.hero"
                slot="image"
              />
              <h3>{{ project.fields.title }}</h3>
              <h4>{{ project.fields.city }}</h4>
              <section v-html="markdown(project.fields.excerpt)"/>
            </project-card>
          </template>
        </project-list>
        <router-link
          :class="$style['showcase--action']"
          to="/projects"
        >
          View Our Work
        </router-link>
      </section>

      <section
        :class="$style.studio"
      >
        <main v-html="markdown(fields.studio)"/>
      </section>
      <section
        :class="$style.members"
      >
        <h2>Our Team</h2>
        <ul :class="$style['studio--list']">
          <template v-for="n in 4">
            <li :class="$style['studio--list--item']">
              <article/>
            </li>
          </template>
        </ul>
      </section>
    </main>

    <rga-cta />

    <rga-footer />

  </article>
</template>

<script>
  import { isEmpty } from 'ramda';

  import { createNamespacedHelpers } from 'vuex';
  const { mapState, mapActions } = createNamespacedHelpers('company');

  import CfImage from '@/components/CFImage';
  import ProjectList from '@/components/ProjectList';
  import ProjectCard from '@/components/RGACard';
  import RgaCta from '@/components/RGACallToAction';
  import RgaFooter from '@/components/RGAFooter';
  import NavigationBar from '@/components/NavigationBar';

  export default {
    name: 'home',

    components: {
      CfImage,
      RgaFooter,
      ProjectCard,
      NavigationBar,
      ProjectList,
      RgaCta,
    },

    computed: {
      ...mapState({
        company: 'entity',
      }),

      fields(){
        return this.company.fields;
      }
    },

    methods: {
      ...mapActions({
        fetchCompany: 'fetch',
      }),

      isEmpty,
    },

    created(){
      this.fetchCompany();
    },
  };
</script>

<style module>
  .container {
    --spacing: 3rem;

    display: grid;
    grid-template-columns: var(--outer-max-width);
    grid-gap: var(--spacing);
    justify-content: center;

    box-sizing: border-box;
    padding: var(--spacing);
  }

  .header {
    display: grid;
    grid-template-rows: 3rem auto;
    grid-gap: calc( var(--spacing) / 3 );
    justify-items: center;
  }

  .logo {
    width: auto;
    height: 100%;
  }

  .content {
    display: grid;
    grid-gap: var(--spacing);
    justify-items: center;
  }

  .introduction {
    display: grid;
    grid-template-columns: var(--inner-max-width);
    justify-content: center;
  }

  .showcase {
    justify-self: stretch;

    display: grid;
    grid-gap: calc( var(--spacing) / 3 );
  }

  .projects {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-gap: calc( var(--spacing) / 3 );

    height: 40rem;
  }

  .projects > * {
    grid-column: span 2;

    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
  }

  .projects > *:nth-child(-n+2){
    grid-column: span 3;
  }


  .showcase--action {
    justify-self: flex-end;
  }

  .studio {
    display: grid;
    grid-template-columns: var(--inner-max-width);
    justify-items: center;
    grid-gap: calc( var(--spacing) / 3 );
  }

  .members {
    display: grid;
    grid-template-columns: var(--outer-max-width);
    grid-gap: calc( var(--spacing) / 3 );
  }

  .studio--list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: calc( var(--spacing) / 3 );

    min-height: 25rem;
  }

  .studio--list--item {
    background-color: #eaeaea;
  }
</style>



