<template>
  <ul
    v-if="!isEmpty(projects)"
  >
    <li
      v-for="project in projects"
      :key="project.sys.id"
    >
      <slot :project="project" />
    </li>
  </ul>
</template>

<script>
  import { isEmpty } from 'ramda';

  export default {
    props: {
      projects: {
        type: Array,
        default: () => [],
      },
    },

    methods: {
      isEmpty,
    }
  };
</script>



