<template>
  <article
    v-if="!isEmpty(company)"
    :class="$style.container"
  >
    <navigation-bar :class="$style.navigation"/>
    <header :class="$style.header">
      <h1 class="heading">Our Work</h1>
    </header>
    <main :class="$style['grid-container']">
      <project-list
        :projects="company.fields.projects"
        :class="$style.grid"
      >
        <template slot-scope="{ project }">
          <project-card
            :to="`/projects/${project.fields.slug}`"
          >
            <category-icon
              slot="icon"
              :category="project.fields.category"
              size="2x"
            />
            <cf-image
              slot="image"
              :asset="project.fields.hero"
            />
            <h3>{{ project.fields.title }}</h3>
            <h4>{{ project.fields.city }}</h4>
            <section v-html="markdown(project.fields.excerpt)"/>
          </project-card>
        </template>
      </project-list>
    </main>
  </article>
</template>

<script>
  import { isEmpty } from 'ramda';

  import PageTitle from '@/components/PageTitle';
  import ProjectList from '@/components/ProjectList';
  import ProjectCard from '@/components/RGACard';
  import CfImage from '@/components/CFImage';
  import NavigationBar from '@/components/NavigationBar';
  import CategoryIcon from '@/components/CategoryIcon';

  import { createNamespacedHelpers } from 'vuex';
  const { mapState, mapActions } = createNamespacedHelpers('company');

  export default {
    name: 'Projects',

    components: {
      ProjectList,
      PageTitle,
      ProjectCard,
      CfImage,
      NavigationBar,
      CategoryIcon,
    },

    computed: {
      ...mapState({
        company: 'entity',
      }),
    },

    methods: {
      ...mapActions({
        fetchCompany: 'fetch',
      }),

      isEmpty,
    },

    created(){
      this.fetchCompany();
    },
  }
</script>

<style module>
  .container {
    display: grid;
    grid-template-columns: var(--outer-max-width);
    justify-content: center;
    grid-gap: var(--grid-spacing);

    box-sizing: border-box;
    padding: 3rem;
  }

  .header {
    display: grid;
    grid-template-columns: var(--outer-max-width);
    justify-content: center;
  }

  .grid-container {
    display: grid;
    grid-template-columns: var(--outer-max-width);
    justify-content: center;
  }

  .grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: var(--grid-spacing);
  }

  .grid > * {
    display: grid;
  }
</style>



