<template>
  <router-link
    tag="article"
    :class="$style.container"
    v-bind="$attrs"
  >
    <figure :class="$style.icon">
      <slot name="icon"/>
    </figure>
    <figure :class="$style.hero">
      <slot name="image"/>
    </figure>
    <main :class="$style.caption">
      <slot />
    </main>
  </router-link>
</template>

<script>
  export default {
    inheritAttrs: false,
  };
</script>

<style module>
  .container {
    --margin: 1rem;

    position: relative;

    display: grid;
    grid-template-rows: 10rem auto;
    background-color: #fafafa;

    cursor: pointer;
    box-shadow: 0.05rem 0.15rem 0.33rem transparent;
    transition: 150ms ease-in-out;
  }

  .container:hover{
    box-shadow: 0.15rem 0.15rem 0.33rem rgba(0, 0, 0, 0.15);
    transform: scale(1.01);
  }

  .icon {
    position: absolute;
    top: 0;
    left: 0;
    margin: var(--margin);
  }

  .hero {
    display: grid;

    background-color: #eaeaea;
  }

  .hero > img,
  .floating > img {
    width: 100%;
    height: 100%;

    object-fit: cover;
    object-position: center;
  }

  .caption {
    display: grid;
    align-content: flex-end;
    justify-content: flex-start;
    grid-gap: 0.33rem;

    box-sizing: border-box;
    padding: var(--margin);
  }
</style>



