import api from '@/utils/contentful';
import { head } from 'ramda';

export default {
  namespaced: true,
  
  state: {
    entity: {},
  },

  mutations: {
    receive(state, { items }){
      state.entity = head(items);
    }
  },

  actions: {
    async fetch({ commit }){
      const response = await api.getEntries({
        content_type: 'company'
      });

      commit('receive', response);
    }
  }
};



