<template>
  <article v-if="!isEmpty(project)" :class="$style.container">
    <navigation-bar :class="$style.navigation" />

    <figure :class="$style.hero">
      <cf-image :asset="project.fields.hero" />
    </figure>

     <header :class="$style.header">
      <h1 class="heading">
        {{ project.fields.title }}
      </h1>
      <h2 class="subheading">
        {{ project.fields.city }}
      </h2>
    </header>

    <main
      :class="$style.body"
    >
      <section
        :class="$style.description"
        v-html="markdown(project.fields.description)"
      />
      <project-info
        :class="$style.info"
        :client="project.fields.client"
        :completed="project.fields.completed"
        :services="project.fields.services"
      />
    </main>

    <project-map
      :class="$style.map"
      :location="project.fields.location"
    />

    <ul :class="$style.gallery">
      <template v-for="(image, index) in project.fields.images">
        <li
          :class="$style['gallery-item']"
          @click="openLightbox(index)"
        >
          <cf-image :asset="image"/>
        </li>
      </template>
    </ul>

    <cf-lightbox
      :assets="project.fields.images"
    >
    </cf-lightbox>
  </article>
</template>

<script>
  import { isEmpty } from 'ramda';
  import store from '@/store/index';

  import { createNamespacedHelpers } from 'vuex';
  const { mapState, mapGetters, mapMutations, mapActions } = createNamespacedHelpers('project');

  import CfImage from '@/components/CFImage';
  import CfLightbox from '@/components/CFLightbox';

  import NavigationBar from '@/components/NavigationBar';
  import ProjectInfo from '@/components/ProjectInfo';
  import ProjectMap from '@/components/ProjectMap';
  import CategoryIcon from '@/components/CategoryIcon';

  import FaIcon from '@fortawesome/vue-fontawesome';
  import farArrowAltCircleLeft from '@fortawesome/fontawesome-free-regular/faArrowAltCircleLeft';

  export default {
    components: {
      CategoryIcon,
      NavigationBar,
      CfImage,
      FaIcon,
      CfLightbox,
      ProjectInfo,
      ProjectMap,
    },

    computed: {
      ...mapState({
        project: 'entity',
      }),

      farArrowAltCircleLeft(){
        return farArrowAltCircleLeft;
      },

      fasTimes(){
        return fasTimes;
      },
    },

    methods: {
      ...mapActions({
        fetchProject: 'fetch'
      }),

      ...mapMutations({
        clearProject: 'clear'
      }),

      openLightbox(index){
        this.$store.dispatch('project/lightbox/open', index);
      },
      isEmpty
    },

    beforeRouteEnter({ params }, from, next){
      store.dispatch('project/fetch', params);
      next();
    },

    beforeRouteUpdate({ params }, from, next){
      this.fetchProject(params);
      next();
    },

    beforeRouteLeave(to, from, next){
      this.clearProject();
      next();
    }
  }
</script>

<style module>
  .container {
    --spacing: 2rem;

    display: grid;
    grid-template-columns: var(--outer-max-width);
    grid-gap: var(--spacing);
    grid-template-rows:
      auto
      minmax(20em, 60vh)
      min-content
      auto
      20em
      auto
    ;
    justify-content: center;
  }

  .navigation {
    box-sizing: border-box;
    padding-top: 1rem;
  }

  .header{
    display: grid;
  }

  .hero {
  }

  .hero > * {
    object-fit: cover;
    object-position: center;

    width: 100%;
    height: 100%;
  }

  .body {
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-gap: var(--spacing);
  }

  .info {
    color: #444444;
  }

  .description {
  }

  .gallery {
    list-style: none;

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-flow: row;
    grid-auto-rows: 20rem;
    grid-gap: calc( var(--spacing) / 3 );
  }

  .gallery-item {
    cursor: pointer;

    cursor: pointer;
    box-shadow: 0.05rem 0.15rem 0.33rem transparent;
    transition: 150ms ease-in-out;
  }

  .gallery-item:hover{
    box-shadow: 0.15rem 0.15rem 0.33rem rgba(0, 0, 0, 0.15);
    transform: scale(1.01);
  }

  .gallery-item > img {
    width: 100%;
    height: 100%;

    object-fit: cover;
    object-position: center;
  }

  .map {
  }
</style>



