import api from '@/utils/contentful';
import lightbox from './lightbox';
import { head, prop, isEmpty } from 'ramda';

export default {
  namespaced: true,

  modules: {
    lightbox
  },

  state: {
    entity: {},
    map: {},
    isLightboxOpen: true
  },

  mutations: {
    clear(state){
      state.entity = {};
    },

    receive(state, payload){
      state.entity = head(payload.items);
    },

    setMap(state, ref){
      state.map = ref;
    },
  },

  actions: {
    async fetch({ commit }, { slug }){
      const response = await api.getEntries({
        content_type: 'project',
        'fields.slug': slug,
      });

      commit('receive', response);
    }
  }
}



