<template>
  <div v-if="!isEmpty(company)" :class="$style.container">
    <cf-image
      :class="$style.logo"
      :asset="company.fields.logo"
    />
    <router-link to="/" tag="h1">
      <a>{{ company.fields.name }}</a>
    </router-link>
  </div>
</template>

<script>
  import { createNamespacedHelpers } from 'vuex';
  const { mapState, mapActions } = createNamespacedHelpers('company');

  import CfImage from '@/components/CFImage';

  export default {
    components: {
      CfImage,
    },

    computed: {
      ...mapState({
        company: 'entity',
      }),
    },

    methods: {
      ...mapActions({
        fetchCompany: 'fetch'
      }),
    },

    created(){
      this.fetchCompany();
    }
  };
</script>

<style module>
  .container {
    display: grid;
    grid-template-columns: 1rem max-content;
    grid-template-rows: 1fr;
    grid-gap: 1em;
    align-items: center;
  }

  .title {
    font-size: 2vw;
  }

  .logo {
    object-fit: contain;
    object-position: center;

    width: 100%;
    height: 100%;
  }
</style>



